@font-face {
  font-family: Roboto;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: block;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/roboto-light.woff2") format("woff2"), url("../fonts/roboto-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: block;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: block;
}
/* stylelint-disable-line at-rule-no-unknown */ /* ================== ТИПОГРАФИКА ================== */
.page {
  font-family: Roboto, Arial, "Helvetica Neue", sans-serif;
  font-size: 16px;
  color: #ffffff;
}
.page * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.page body {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100vw;
  min-height: 100vh;
  background-color: #231f20;
  overflow-x: hidden;
}
.page__inner {
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.page__content {
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 1440px;
}
.page__main {
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  align-self: stretch;
  justify-content: center;
  padding: 20px;
}
.page__title {
  margin: 0 0 2em;
  text-align: center;
  font-weight: 400;
  line-height: 1.4em;
  font-size: 2.5rem;
}
.page__title-company {
  display: block;
  font-weight: 500;
}
.page__title-company--highlight {
  display: inline;
  color: #01b2ff;
  font-size: 1.2em;
  line-height: 1.5em;
}
.page__text {
  margin: 0 0 1rem;
  text-align: center;
  font-size: 1rem;
  font-weight: 300;
}
.page__text--reconstruction {
  font-size: 1.25rem;
}
.page__text--eng {
  margin-top: 4rem;
  font-size: 1.5rem;
  line-height: 1.5;
}
.page__text--eng-date {
  margin: 0 0 1rem;
  font-size: 1.25rem;
}
.page__email {
  margin: 1em 0;
  text-align: center;
  font-size: 1rem;
  font-weight: 300;
}
.page__email-link {
  margin: 0 0 0 0.5em;
  text-decoration: none;
  color: #01b2ff;
  font-weight: 500;
  transition: opacity 0.25s ease-in;
}
.page__email-link:hover {
  opacity: 0.75;
}
.page__email-link:active, .page__email-link:visited {
  color: #01b2ff;
  opacity: 1;
}

@media screen and (max-width: 1439px) {
  .page__content {
    width: 768px;
  }
  .page__main {
    padding: 15px;
  }
}
@media screen and (max-width: 767px) {
  .page__content {
    width: 100%;
  }
  .page__main {
    padding: 15px;
  }
  .page__title {
    font-size: 1.75rem;
  }
  .page__text {
    font-size: 0.875rem;
  }
  .page__text--reconstruction {
    font-size: 1.175rem;
  }
  .page__email {
    font-size: 0.875rem;
  }
}
.header {
  position: relative;
  width: 1440px;
  padding: 20px;
}
.header::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: calc((1440px - 100vw) / 2);
  width: 100vw;
  height: 5px;
  background-color: #01b2ff;
}
.header__logo {
  width: 90px;
  height: 90px;
}

@media screen and (max-width: 1439px) {
  .header {
    width: 768px;
    padding: 15px;
  }
  .header::before {
    left: calc((768px - 100vw) / 2);
  }
}
@media screen and (max-width: 767px) {
  .header {
    width: 100%;
    padding: 15px;
  }
  .header::before {
    left: calc((100% - 100vw) / 2);
  }
}
.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: #01b2ff;
}
.footer__copyright {
  width: 1440px;
  padding: 20px;
  font-size: 0.75rem;
  font-weight: 300;
}

@media screen and (max-width: 1439px) {
  .footer__copyright {
    width: 768px;
    padding: 15px;
  }
}
@media screen and (max-width: 767px) {
  .footer__copyright {
    width: 100%;
    padding: 15px;
    font-size: 0.6875rem;
  }
}